import React from 'react';
import './Atributos.css'

import Atributo from './Atributo';

const Atributos = () => {
    return(
        <div className="stats">
            <header className="stats-header">
                <span className="stats-title"><h2>Atributos</h2></span>
                <span className="stats-base">Base</span>
                <span className="stats-bono">Bonos</span>
            </header>
            <Atributo title="AGI" value="10" bonus="15"/>
            <Atributo title="CON" value="10" bonus="15"/>
            <Atributo title="DES" value="10" bonus="15"/>
            <Atributo title="FUE" value="10" bonus="15"/>
            <Atributo title="INT" value="10" bonus="15"/>
            <Atributo title="PER" value="10" bonus="15"/>
            <Atributo title="POD" value="10" bonus="15"/>
            <Atributo title="VOL" value="10" bonus="15"/>
        </div>
    );
}

export default Atributos;