import React from 'react';

const HCombate = (props) => {
    return(
        <div className="combat-skill">
            <span className="combat-skill-cost">{ props.cost }</span>
            <span className="combat-skill-name">{ props.name }</span>
            <span className="combat-skill-stat">{ props.stat }</span>
            <input className="combat-value" placeholder="Base" type="text" name="" id=""/>+
            <input className="combat-value" placeholder="Bono" type="text" name="" id=""/>+
            <input className="combat-value" placeholder="Cat" type="text" name="" id=""/>+
            <input className="combat-value" placeholder="Esp" type="text" name="" id=""/>=
            <input className="combat-value combat-final" placeholder="Final" type="text" name="" id=""/>
        </div>
    );
}

export default HCombate;