import React from 'react';

import './Resistencia.css';

const Resistencia = (props) => {
    return(
        <div className="resistencia">
            <span className="resistencia-name">{ props.name }</span>
            <span className="resistencia-stat">{ props.stat }</span>
            <input className="resist-value" type="text" name="" id="" placeholder="PRES" />
            <input className="resist-value" type="text" name="" id="" placeholder="BONO" />
            <input className="resist-value" type="text" name="" id="" placeholder="ESP" />
            <input className="resist-value" type="text" name="" id="" placeholder="VAR" />
            <input className="resist-value" type="text" name="" id="" placeholder="FINAL" />
        </div>
    );
}

export default Resistencia;