import React from 'react';
import './Habilidad.css';

const Habilidad = (props) => {
    return(
        <div className="habilidad">
            <span className="kill-cost">{props.cost}</span>
            <span className="skill-name">{props.skillName}</span>
            <span className="skill-stat">{props.stat}</span>
            <input className="skill-value skill-base" type="text" name="" id="" placeholder="Base" />+
            <input className="skill-value skill-bono" type="text" name="" id="" placeholder="Bono" />+
            <input className="skill-value skill-mnat" type="text" name="" id="" placeholder="M Nat" />+
            <input className="skill-value skill-bnat" type="text" name="" id="" placeholder="B Nat" />=
            <input className="skill-value skill-total" type="text" name="" id="" placeholder="Total" />+
            <input className="skill-value skill-cat" type="text" name="" id="" placeholder="Cat" />+
            <input className="skill-value skill-esp" type="text" name="" id="" placeholder="Esp" />=
            <input className="skill-value skill-final" type="text" name="" id="" placeholder="Final" />
        </div>
    );
}

export default Habilidad;