import React from 'react';
import Resistencia from './Resistencia';

import './Resistencias.css';

const Resistencias = () => {
    return(
        <div className="resistencias">
            <h2 className="header">
                Resistencias
            </h2>
            <Resistencia name="FISICA" stat="CON" />
            <Resistencia name="ENFERMEDADES" stat="CON" />
            <Resistencia name="VENENOS" stat="CON" />
            <Resistencia name="MAGICA" stat="POD" />
            <Resistencia name="PSIQUICA" stat="VOL" />
        </div>
    );
}

export default Resistencias;