import React from 'react';
import './Atributo.css';

const Atributo = (props) => {
    return(
        <div className="stat">
            <div className="value stat-title">{props.title}</div>
            <input type="text" name="" id="" className="value stat-base"/>
            <div className="value stat-bonus">{props.bonus}</div>
        </div>
    );
}

export default Atributo;