import React from 'react';

import HCombate from './HCombate';
import './Combat.css'

const Combate = () => {
    return(
        <div className="combat">
            <div className="combat-header">
                <h2>Habilidades de Combate</h2>
            </div>
            <div className="combat-skills">
                <HCombate cost="1" name="Ataque" stat="DES"/>
                <HCombate cost="1" name="Parada" stat="DES"/>
                <HCombate cost="1" name="Esquiva" stat="AGI"/>
                <HCombate cost="1" name="Llevar Armadira" stat="FUE"/>
            </div>
        </div>
    );
}

export default Combate;